import { useState } from 'react';
import { useApp } from '../context/AppContext';

const Tickets = () => {
  const { data, addItem, updateItem, deleteItem, currentUser } = useApp();
  const [showForm, setShowForm] = useState(false);
  const [editingTicket, setEditingTicket] = useState<any>(null);
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    priority: 'medium',
    status: 'open',
    assignedTo: '',
    customerId: ''
  });

  const handleSubmit = (e: any) => {
    e.preventDefault();
    const ticketData = {
      ...formData,
      createdBy: currentUser?.id,
      createdAt: new Date().toISOString()
    };
    
    if (editingTicket) {
      updateItem('tickets', editingTicket.id, ticketData);
    } else {
      addItem('tickets', ticketData);
    }
    resetForm();
  };

  const resetForm = () => {
    setFormData({
      title: '',
      description: '',
      priority: 'medium',
      status: 'open',
      assignedTo: '',
      customerId: ''
    });
    setShowForm(false);
    setEditingTicket(null);
  };

  const handleEdit = (ticket: any) => {
    setFormData(ticket);
    setEditingTicket(ticket);
    setShowForm(true);
  };

  return (
    <div className="p-2 sm:p-4">
      <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center mb-4 sm:mb-6 gap-3">
        <h2 className="text-xl sm:text-2xl font-bold text-gray-900">Support Tickets</h2>
        <button
          onClick={() => setShowForm(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-2 sm:px-4 sm:py-2 rounded-lg text-sm sm:text-base"
        >
          Create Ticket
        </button>
      </div>

      {showForm && (
        <div className="bg-white p-6 rounded-lg border border-gray-200 mb-6">
          <h3 className="text-lg font-semibold mb-4">
            {editingTicket ? 'Edit' : 'Add'} Item
          </h3>
          <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <input
              type="text"
              placeholder="Ticket Title"
              value={formData.title}
              onChange={(e: any) => setFormData({...formData, title: e.target.value})}
              className="p-3 border border-gray-300 rounded-lg"
              required
            />
            <select
              value={formData.priority}
              onChange={(e: any) => setFormData({...formData, priority: e.target.value})}
              className="p-3 border border-gray-300 rounded-lg"
            >
              <option value="low">Low</option>
              <option value="medium">Medium</option>
              <option value="high">High</option>
              <option value="urgent">Urgent</option>
            </select>
            <select
              value={formData.customerId}
              onChange={(e: any) => setFormData({...formData, customerId: e.target.value})}
              className="p-3 border border-gray-300 rounded-lg"
            >
              <option value="">Select Customer</option>
              {data.customers?.map((customer: any) => (
                <option key={customer.id} value={customer.id}>{customer.name}</option>
              ))}
            </select>
            <select
              value={formData.status}
              onChange={(e: any) => setFormData({...formData, status: e.target.value})}
              className="p-3 border border-gray-300 rounded-lg"
            >
              <option value="open">Open</option>
              <option value="in-progress">In Progress</option>
              <option value="resolved">Resolved</option>
              <option value="closed">Closed</option>
            </select>
            <textarea
              placeholder="Description"
              value={formData.description}
              onChange={(e: any) => setFormData({...formData, description: e.target.value})}
              className="md:col-span-2 p-3 border border-gray-300 rounded-lg"
              rows={4}
              required
            />
            <div className="md:col-span-2 flex gap-2">
              <button type="submit" className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg">
                {editingTicket ? 'Edit' : 'Add'} Item Ticket
              </button>
              <button type="button" onClick={resetForm} className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded-lg">
                Cancel
              </button>
            </div>
          </form>
        </div>
      )}

      {/* Mobile Cards View */}
      <div className="block sm:hidden space-y-3">
        {data.tickets?.map((ticket: any) => {
          const customer = data.customers?.find((c: any) => c.id == ticket.customerId);
          return (
            <div key={ticket.id} className="bg-white p-4 rounded-lg border border-gray-200">
              <div className="flex justify-between items-start mb-2">
                <h3 className="font-semibold text-gray-900 text-sm">{ticket.title}</h3>
                <div className="flex gap-1">
                  <button
                    onClick={() => handleEdit(ticket)}
                    className="bg-blue-500 hover:bg-blue-600 text-white px-2 py-1 rounded text-xs"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => deleteItem('tickets', ticket.id)}
                    className="bg-red-500 hover:bg-red-600 text-white px-2 py-1 rounded text-xs"
                  >
                    Delete
                  </button>
                </div>
              </div>
              <p className="text-sm text-gray-600 mb-2">{customer?.name || 'N/A'}</p>
              <div className="flex gap-2 mb-2">
                <span className={`px-2 py-1 rounded text-xs ${
                  ticket.priority === 'urgent' ? 'bg-red-100 text-red-800' :
                  ticket.priority === 'high' ? 'bg-orange-100 text-orange-800' :
                  ticket.priority === 'medium' ? 'bg-yellow-100 text-yellow-800' :
                  'bg-green-100 text-green-800'
                }`}>
                  {ticket.priority}
                </span>
                <span className={`px-2 py-1 rounded text-xs ${
                  ticket.status === 'closed' ? 'bg-gray-100 text-gray-800' :
                  ticket.status === 'resolved' ? 'bg-green-100 text-green-800' :
                  ticket.status === 'in-progress' ? 'bg-blue-100 text-blue-800' :
                  'bg-yellow-100 text-yellow-800'
                }`}>
                  {ticket.status}
                </span>
              </div>
              <p className="text-xs text-gray-500">{new Date(ticket.createdAt).toLocaleDateString()}</p>
            </div>
          );
        })}
      </div>

      {/* Desktop Table View */}
      <div className="hidden sm:block bg-white rounded-lg border border-gray-200 overflow-x-auto">
        <table className="w-full">
          <thead className="bg-gray-50">
            <tr>
              <th className="p-3 text-left text-sm font-medium">Title</th>
              <th className="p-3 text-left text-sm font-medium">Customer</th>
              <th className="p-3 text-left text-sm font-medium">Priority</th>
              <th className="p-3 text-left text-sm font-medium">Status</th>
              <th className="p-3 text-left text-sm font-medium">Created</th>
              <th className="p-3 text-left text-sm font-medium">Actions</th>
            </tr>
          </thead>
          <tbody>
            {data.tickets?.map((ticket: any) => {
              const customer = data.customers?.find((c: any) => c.id == ticket.customerId);
              return (
                <tr key={ticket.id} className="border-t">
                  <td className="p-3 text-sm">{ticket.title}</td>
                  <td className="p-3 text-sm">{customer?.name || 'N/A'}</td>
                  <td className="p-3">
                    <span className={`px-2 py-1 rounded text-xs ${
                      ticket.priority === 'urgent' ? 'bg-red-100 text-red-800' :
                      ticket.priority === 'high' ? 'bg-orange-100 text-orange-800' :
                      ticket.priority === 'medium' ? 'bg-yellow-100 text-yellow-800' :
                      'bg-green-100 text-green-800'
                    }`}>
                      {ticket.priority}
                    </span>
                  </td>
                  <td className="p-3">
                    <span className={`px-2 py-1 rounded text-xs ${
                      ticket.status === 'closed' ? 'bg-gray-100 text-gray-800' :
                      ticket.status === 'resolved' ? 'bg-green-100 text-green-800' :
                      ticket.status === 'in-progress' ? 'bg-blue-100 text-blue-800' :
                      'bg-yellow-100 text-yellow-800'
                    }`}>
                      {ticket.status}
                    </span>
                  </td>
                  <td className="p-3 text-sm">{new Date(ticket.createdAt).toLocaleDateString()}</td>
                  <td className="p-3">
                    <button
                      onClick={() => handleEdit(ticket)}
                      className="bg-blue-500 hover:bg-blue-600 text-white px-3 py-1 rounded mr-2 text-sm"
                    >
                      Edit
                    </button>
                    <button
                      onClick={() => deleteItem('tickets', ticket.id)}
                      className="bg-red-500 hover:bg-red-600 text-white px-3 py-1 rounded text-sm"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Tickets;